import { Component ,ViewChild} from '@angular/core';
import { Nav, Platform ,Events} from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { Storage } from '@ionic/storage';
// import {TabsPage} from '../pages/tabs/tabs';
// import {CartsPage} from '../pages/carts/carts';

export interface PageInterface {
  title: string;
  name: string;
  component: any;
  icon: string;
  logsOut?: boolean;
  index?: number;
  tabName?: string;
  tabComponent?: any;
}
@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  public isLarge = false;
  @ViewChild(Nav) nav: Nav;

  rootPage:any;
  //showSearchbar: boolean = false;
  //myInput:any;
  public footerIsHidden: boolean = false;
  public headerIsHidden: boolean = false;

  public path:any;

  public firstname:any;
  public loguser:any;
 public utype:any;

  constructor(platform: Platform,
    private storage: Storage, statusBar: StatusBar,public events: Events) {
      platform.ready().then(()=>{
        events.subscribe('hideFooter', (data) => {
          this.footerIsHidden = data.isHidden;
        })
        events.subscribe('hideHeader', (data) => {
          this.headerIsHidden = data.isHidden;
        })
     

      this.loguser =  JSON.parse(localStorage.getItem('userData'));  
      //console.log(this.loguser) ;
     if(this.loguser){
      
       this.firstname = this.loguser.first_name +' '+ this.loguser.last_name;
        this.utype = this.loguser.utype
        this.nav.setRoot('HomePage');
     }
     else{
      this.nav.setRoot('AfterSplashPage');
     }
})
  }



  ionViewDidLoad() {
   
    
  }


  

  // toggleSearchbar() { 
  //   this.showSearchbar = !this.showSearchbar;
  //   //this.content.resize();
  // }
  // search_favourite(value){
  //   console.log('here');
  //   console.log(this.myInput)
  // }
  // onCancel(value)
  // {
  //   this.showSearchbar = !this.showSearchbar;
  //   this.myInput = '';
  //   console.log(this.myInput);
  //   console.log('cancel');
  // }





	public home(){
		this.nav.push('HomePage');
	}
	public fabourite(){
		this.nav.push('FabouritePage');
	}
	public post(){
		this.nav.push('MypostPage');
	}
	public search(){
		this.nav.push('SearchPage');
	}
  public sellbike(){
    this.nav.push('SellbikeOnePage');
	}
	public dtls(){
		this.nav.push('AdDetailsPage');
	}
	public searchvehicle(){
		this.nav.push('SearchVehiclePage');
	}
  
  public logout(){
    this.storage.ready().then(() => {
      const data=localStorage.getItem("userData");
localStorage.removeItem('userData');
localStorage.setItem('userData',"");
    this.storage.set("uid","");
   
   this.nav.setRoot('AfterSplashPage');
  });
}


    
  public goToPage(page){
    this.nav.setRoot(page);
  }




}




